import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import omit from 'omit.js';
import './index.less';

export type Omit<T, K extends keyof T> = Pick<T, Exclude<keyof T, K>>;
export const tuple = <T extends string[]>(...args: T) => args;

const ButtonTypes = tuple('default', 'primary', 'ghost', 'dashed', 'danger', 'links');
export type ButtonType = (typeof ButtonTypes)[number];
const ButtonShapes = tuple('circle', 'circle-outline', 'round');
export type ButtonShape = (typeof ButtonShapes)[number];
const ButtonSizes = tuple('large', 'default', 'small');
export type ButtonSize = (typeof ButtonSizes)[number];
const ButtonHTMLTypes = tuple('submit', 'button', 'reset');
export type ButtonHTMLType = (typeof ButtonHTMLTypes)[number];

export interface BaseButtonProps {
  /**
   * 按钮有五种类型：主按钮、次按钮、虚线按钮、危险按钮和链接按钮
   * @default 按钮类型
   */
  type?: ButtonType;
  /**
   * 待补充
   * @default 未实现
   */
  icon?: string;
  /**
   * 待补充
   * @default 未实现
   */
  shape?: ButtonShape;
  /**
   * 按钮有大、中、小三种尺寸。默认为中
   * @default 按钮尺寸
   */
  size?: ButtonSize;
  /**
   * 设置按钮的loading状态
   * @default loading状态
   */
  loading?: boolean | { delay?: number };
  /**
   * 待补充
   * @default 未实现
   */
  prefixCls?: string;
  /**
   * 类名
   * @default 类名
   */
  className?: string;
  /**
   * 待补充
   * @default 未实现
   */
  ghost?: boolean;
  /**
   * 待补充
   * @default 未实现
   */
  block?: boolean;
  /**
   * 按钮内容
   * @default 按钮内容
   */
  children?: React.ReactNode;
}

// Typescript will make optional not optional if use Pick with union.
// Should change to `AnchorButtonProps | NativeButtonProps` and `any` to `HTMLAnchorElement | HTMLButtonElement` if it fixed.
// ref: https://github.com/ant-design/ant-design/issues/15930
export type AnchorButtonProps = {
  href: string;
  target?: string;
  onClick?: (values: any) => Promise<any> | void;
} & BaseButtonProps &
  Omit<React.AnchorHTMLAttributes<any>, 'type' | 'onClick'>;

export type NativeButtonProps = {
  htmlType?: ButtonHTMLType;
  onClick?: (values: any) => Promise<any> | void;
} & BaseButtonProps &
  Omit<React.ButtonHTMLAttributes<any>, 'type' | 'onClick'>;

export type ButtonProps = Partial<AnchorButtonProps & NativeButtonProps>;

interface ButtonState {
  loading?: boolean | { delay?: number };
  hasTwoCNChar: boolean;
}

class Button extends React.Component<ButtonProps, ButtonState> {
  static defaultProps = {
    loading: false,
    ghost: false,
    block: false,
    htmlType: 'button'
  };

  static propTypes = {
    type: PropTypes.string,
    shape: PropTypes.oneOf(ButtonShapes),
    size: PropTypes.oneOf(ButtonSizes),
    htmlType: PropTypes.oneOf(ButtonHTMLTypes),
    onClick: PropTypes.func,
    loading: PropTypes.oneOfType([PropTypes.bool, PropTypes.object]),
    className: PropTypes.string,
    icon: PropTypes.string,
    block: PropTypes.bool,
    title: PropTypes.string
  };

  static getDerivedStateFromProps(nextProps: ButtonProps, prevState: ButtonState) {
    if (nextProps.loading instanceof Boolean) {
      return {
        ...prevState,
        loading: nextProps.loading
      };
    }
    return null;
  }

  private delayTimeout: number;

  constructor(props: ButtonProps) {
    super(props);
    this.state = {
      loading: props.loading,
      hasTwoCNChar: false
    };
  }

  componentDidUpdate(prevProps: ButtonProps) {
    if (prevProps.loading && typeof prevProps.loading !== 'boolean') {
      clearTimeout(this.delayTimeout);
    }

    const { loading } = this.props;
    if (loading && typeof loading !== 'boolean' && loading.delay) {
      this.delayTimeout = window.setTimeout(() => this.setState({ loading }), loading.delay);
    } else if (prevProps.loading !== this.props.loading) {
      // eslint-disable-next-line react/no-did-update-set-state
      this.setState({ loading });
    }
  }

  componentWillUnmount() {
    if (this.delayTimeout) {
      clearTimeout(this.delayTimeout);
    }
  }

  handleClick: (values: any) => Promise<any> | void = (e) => {
    const { loading } = this.state;
    const { onClick } = this.props;
    if (loading) {
      return;
    }
    if (onClick) {
      const promise = onClick(e);
      if (promise && promise.then) {
        this.setState({
          loading: true
        });
        promise.then(
          () => {
            this.setState({
              loading: false
            });
          },
          () => {
            this.setState({
              loading: false
            });
          }
        );
      }
    }
  };

  render() {
    const { type, shape, size, className, children, icon, ghost, block, ...rest } = this.props;
    const { loading } = this.state;

    // large => lg
    // small => sm
    let sizeCls = '';
    switch (size) {
      case 'large':
        sizeCls = 'large';
        break;
      case 'small':
        sizeCls = 'small';
        break;
      default:
        break;
    }

    const iconType = loading ? 'loading' : icon;
    const prefixCls = 'btn';

    const classes = classNames(prefixCls, className, {
      [`${prefixCls}-${type}`]: type,
      [`${prefixCls}-${shape}`]: shape,
      [`${prefixCls}-${sizeCls}`]: sizeCls,
      [`${prefixCls}-icon-only`]: !children && children !== 0 && iconType,
      [`${prefixCls}-loading`]: loading,
      [`${prefixCls}-background-ghost`]: ghost,
      [`${prefixCls}-block`]: block
    });

    const kids = children;

    // @ts-ignore
    const linkButtonRestProps = omit(rest as AnchorButtonProps, ['htmlType', 'loading']);
    if (linkButtonRestProps.href !== undefined) {
      return (
        <a {...linkButtonRestProps} className={classes} onClick={this.handleClick}>
          {kids}
        </a>
      );
    }
    // React does not recognize the `htmlType` prop on a DOM element. Here we pick it out of `rest`.
    const { htmlType, ...otherProps } = rest as NativeButtonProps;

    return (
      <button
        {...(omit(otherProps, ['loading']) as NativeButtonProps)}
        type={htmlType}
        className={classes}
        onClick={this.handleClick}
      >
        {loading && <span className="animate-loading" />}
        <span>{kids}</span>
      </button>
    );
  }
}

export default Button;
